import "./App.css"
import MenuNav from "./components/MenuNav";
import MainPart from "./components/MainPart";
import {Container, Row} from "react-bootstrap";
import {
    BrowserRouter,
    Routes,
    Route
} from "react-router-dom";


function App() {
    return (
        <div className="page">
            <MenuNav/>
            <Container className={"main"}>
                <Row className="justify-content-md-center mt-2">
                    <BrowserRouter>
                        <Routes>
                            <Route path="/" element={<MainPart action={"today"}/>}/>
                            <Route path="/today" element={<MainPart action={"today"}/>}/>
                            <Route path="/inbox" element={<MainPart action={"inbox"}/>}/>
                        </Routes>
                    </BrowserRouter>
                </Row>
            </Container>
        </div>

    );
}

export default App;
