import React from 'react';
import {Col, Row} from "react-bootstrap";

const EveryTask = (props) => {
    return (
        <div className={"task"}>
            <Row>
                <h4>
                    {props.name}
                </h4>
            </Row>
            <Row>
                <i>
                    {props.desc}
                </i>
            </Row>
            <Row>
            </Row>
        </div>
    )
};

export default EveryTask;