import React, {useEffect, useState} from 'react'
import {Button, Container, Row} from "react-bootstrap";
import axios from "axios";
import TaskView from "./TaskView";
import EveryTask from "./EveryTask";

const MainPart = (props) => {
    const [showTask, setShowTask] = useState(false);
    const [list, setList] = useState([])
    useEffect(() => {
        axios({
            method: "GET",
            url: "http://127.0.0.1:8000/api/to_do_list/"
        }).then(response => {
            setList(response["data"])
            console.log(response)
        })
    }, [])

    function hideShowTask() {
        setShowTask(!showTask)
    }

    let Today = new Date()
    return (
        <Container>

            {props.action === "today" && (
                <div>
                    <h2>Today</h2>
                    <i>{Today.toDateString()}</i>
                </div>
            )}
            {props.action === "inbox" && (
                <div>
                    <h2>Inbox</h2>
                    <Row className={"taskList"}>
                        {list.map(elem => (
                            <EveryTask key={elem.id} name={elem.task} desc={elem.description}/>
                        ))}
                    </Row>
                </div>
            )}

            {showTask && (
                <div>
                    <Row className="mt-2">
                        <TaskView func={hideShowTask}/>
                    </Row>

                </div>
            )}
            {!showTask && (
                <Row className="m-4">
                    <Button variant="warning" size="sm" onClick={() => {
                        hideShowTask()
                    }}>Add task</Button>
                </Row>
            )}
        </Container>
    );
};
export default MainPart